.sc-figure {
  @include ts-small-content;
  box-shadow: 0 0 0 0.75pt #d1d1d1, 0 0 3pt 0.75pt #ccc;
  padding: var(--double-spacing);
}

.sc-figure > .se-legend {
  padding-top: var(--double-spacing);
  @include ts-small-content;
  margin-bottom: var(--default-spacing);
}

.sc-figure > img {
  max-width: 100%;
  margin: 0 auto;
  display: block;
}

.sc-insert-figure-tool > input {
  display: none;
}
