html {
  width: 100%;
  height: 100%;
  overflow: auto;
  position: relative;
  -webkit-font-smoothing: antialiased;
}

body > .sc-file-select {
  display: none;
}

/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}