/*
  Variables
  ---------------------------------
*/

:root {
  /* Colors */
  --substance-background: #F5F6F4;
  --substance-primary-color: #0095B3;
  --default-button-color: #F0F1EC;
  --success-color: #6CBA28;
  --inverted-text-color: #fff;
  --quiet-text-color: rgba(0, 0, 0, 0.50);
  --quiet-inverted-text-color: rgba(255, 255, 255, 0.50);
  --dark-text-color: #212121;
  --gray-text-color: rgba(33,33,33,0.5);
  --link-color: rgb(var(--primary-color));
  --heading-color: rgb(var(--primary-color));
  --published-color: #6CBA28;
  --dark-background-color: #1A1A1A;
  --light-border-color: rgba(0,0,0, 0.1);
  --primary-color: 4, 149, 179;
  --error-color: 225, 56, 56;

  /* Typography */
  --default-font: 'Fira Sans', Arial;
  --heading-font: 'PT Serif', Georgia;

  /* Spacing */
  --default-spacing: 12px;
  --half-spacing: 6px;
  --double-spacing: 24px;
  --quad-spacing: 48px;
  --octal-spacing: 96px;
  --large-width-limiter: 1200px;
  --large-screen-width: 83.33333333%;
  --middle-screen-width: 91.666666666%;
  --header-height: 60px;
  --footer-height: 230px;
  --mobile-footer-height: 370px;
  --content-min-height: calc(100vh - var(--header-height) - var(--footer-height));
  --mobile-content-min-height: calc(100vh - var(--header-height) - var(--mobile-footer-height));
  --input-max-width: 500px;
  --input-height: 36px;
  --large-button-height: 48px;
  --default-button-height: 36px;
  --small-button-height: 26px;
  /* Misc */
  --action-button-border-radius: 13px;
  --action-button-padding: 2px 20px;
  --popover-border-radius: 4px;
  --popover-shadow: rgb(209, 209, 209) 0px 0px 0px 1px, rgb(204, 204, 204) 0px 0px 4px 1px;
  --card-shadow: 0 2px 4px 0 rgba(188, 188, 188, 0.50);

  --modal-small-size: 330px;
  --modal-medium-size: 520px;
  --modal-large-size: 700px;
  --popover-medium-size: 450px;
}