.sc-page-dialog {
  max-width: 500px;
  background: white;
  margin: 0 auto;
  box-shadow: var(--popover-shadow);
  border-radius: 5px;
}

.sc-page-dialog .se-title {
  @include ts-heading-3;
  padding: var(--double-spacing);
}

.sc-page-dialog .se-body {
  @include ts-content;
  padding: var(--default-spacing) var(--double-spacing);
}

.sc-page-dialog .sc-hint {
  border-top: 1px solid #E6E6E6;
  padding: var(--double-spacing);
} 