/* Editor styles, no need to set anything special for reader styles */
.sc-document-page.sm-editor {
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.sc-document-page.sm-editor > .sc-header {
  flex-grow: 0;
  flex-shrink: 0;
  background: #fff;
  z-index: 2;
}

.sc-document-page.sm-editor > .sc-document-content {
  flex-grow: 1;
  overflow: hidden;
}