.sc-version-item {
  @include ts-small-interface;
  padding: var(--default-spacing);
  border-bottom: solid 1px #EAEAEA;
  cursor: pointer;
  margin: 1px;
  /* Attention: user-select=none is problematic for us, because then we do not receive mousedown events anymore */
  user-select: none;
}

.sc-version-item.sm-selected {
  outline: solid 2px #017AFF;
  background: rgba(1, 122, 255, 0.03);
}

.sc-version-item.sm-auto .se-description,
.sc-version-item.sm-current .se-description {
  opacity: 0.5;
}