.sc-feature {
  margin: var(--octal-spacing) 0;
}

.sc-feature.sm-last {
  margin-bottom: 0px;
}

.sc-feature .sc-limiter {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.sc-feature .sc-limiter > * {
  max-width: 50%;
}

.sc-feature .sc-limiter > img {
  max-width: 50%;
}

.sc-feature:nth-child(odd) .sc-limiter {
  flex-direction: row;
}

.sc-feature:nth-child(even) .sc-limiter {
  flex-direction: row-reverse;
}

.sc-feature:nth-child(odd) .se-description {
  padding-left: var(--quad-spacing);
}

.sc-feature:nth-child(even) .se-description {
  padding-right: var(--quad-spacing);
}

.sc-feature .se-description {
  @include ts-content;
}

.sc-feature .se-description > * {
  padding-bottom: var(--default-spacing);
}

.sc-feature.sm-shifted img {
  margin-top: -180px;
}

// Left aligned version, e.g. image are always on the left and smaller
.sc-feature.sm-left-aligned .sc-limiter > * {
  max-width: 70%;
}

.sc-feature.sm-left-aligned .sc-limiter > img {
  max-width: 30%;
}

.sc-feature.sm-left-aligned .se-description {
  padding-left: var(--quad-spacing);
}

.sc-feature.sm-left-aligned .sc-limiter {
  flex-direction: row;
}

.sc-feature.sm-mobile .sc-limiter {
  flex-direction: column!important;
}

.sc-feature.sm-mobile .sc-limiter > * {
  max-width: 100% !important;
}

.sc-feature.sm-mobile .sc-limiter > img {
  width: 500px;
}

.sc-feature.sm-mobile .se-description {
  padding-top: var(--quad-spacing);
  text-align: center;
}

// No padding for description in single column mode
.sc-feature.sm-mobile .se-description {
  padding-left: 0px;
  padding-right: 0px;
}

.sc-feature.sm-mobile.sm-shifted img {
  margin-left: -60px;
}