.sc-discussion {
  margin-top: var(--double-spacing);
}

.sc-discussion > .se-comment-context {
  border-left: 4px solid var(--light-border-color);
  padding: var(--default-spacing);
  @include ts-large-content;
}

.sc-discussion > .se-comment-context .sm-highlight {
  background: rgba(246, 187, 67, 0.35);
}

.sc-discussion > .sc-divider {
  margin: 0 calc(-1 * var(--double-spacing));
}

.sc-discussion > .se-footer {
  @include ts-interface;
  @include ts-quiet;
  padding: var(--double-spacing) 0;
}

.sc-discussion > .se-footer > .sm-login {
  cursor: pointer;
  text-decoration: underline;
}