.sc-radio-toggle .se-radio-option {
  flex: 1 1 0px;
  padding: var(--default-spacing);
  border: 2px solid #DBDBDB;
  border-radius: 5px;
  cursor: pointer;
  @include ts-interface;
}

.sc-radio-toggle .se-radio-option.sm-active {
  border: 2px solid var(--link-color);
}

.sc-radio-toggle .se-radio-option .se-radio-input {
  align-self: baseline;
  margin: 0;
}

.sc-radio-toggle .se-radio-option .se-radio-label {
  flex: 1 1 0;
}

.sc-radio-toggle .se-radio-option .se-radio-label .se-description {
  @include ts-small-interface;
  @include ts-quiet;
}