.sc-dashboard-item {
  margin-bottom: var(--default-spacing);
}

.sc-dashboard-item > .sc-card {
  padding-top: var(--default-spacing);
}

.sc-dashboard-item > .sc-card > .sc-prose {
  padding: 0 var(--double-spacing) var(--half-spacing);
}

.sc-dashboard-item > .sc-card > .sc-horizontal-stack {
  padding: var(--half-spacing) var(--double-spacing);
}

.sc-dashboard-item > .sc-card .sc-icon {
  align-self: baseline;
}