.sc-four-square-layout {
  position: absolute;
  min-width: 800px;
  z-index: 2000;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: #fff;
  display: grid;
  grid-template-columns: 400px auto;	
  grid-template-rows: 60px auto;	
  justify-content: stretch;	
  grid-gap: 0px;
}

.sc-four-square-layout > div {
  overflow: hidden;
}

.sc-four-square-layout > div.se-top-left,	
.sc-four-square-layout > div.se-top-right {	
  border-bottom: 1px solid var(--light-border-color);
}

/* Scrollable side panel */
.sc-four-square-layout > div.se-top-left {
  border-right: 1px solid var(--light-border-color);
}

/* Scrollable content panel */
.sc-four-square-layout > div.se-bottom-left {
  border-right: 1px solid var(--light-border-color);
  overflow: auto;
}

.sc-four-square-layout > div.se-bottom-right {
  overflow: auto;
}