.sc-mobile-header {
  border-bottom: 1px solid var(--light-border-color);
  @include ts-content;
}

.sc-mobile-header .sc-horizontal-stack {
  height: 60px;
}

.sc-mobile-header .se-menu {
  border-top: 1px solid var(--light-border-color);
  padding-bottom: var(--double-spacing);
}

.sc-mobile-header .se-login-status {
  border-bottom: 1px solid var(--light-border-color);
  @include ts-interface;
  @include ts-quiet;
}

.sc-mobile-header .se-menu-header {
  padding-top: var(--double-spacing);
  @include ts-interface;
  @include ts-quiet;
}

.sc-mobile-header .se-menu-item {
  padding: var(--half-spacing) 0;
}


/* Inverted menu, overrides colors */

.sc-mobile-header.sm-inverted {
  @include ts-inverted;
}

.sc-mobile-header.sm-inverted .se-login-status {
  @include ts-inverted;
}

.sc-mobile-header.sm-inverted .se-menu-header {
  @include ts-quiet-inverted;
}

