.sc-question {
  @include ts-content;
  padding: var(--default-spacing) 0;
  // border-bottom: 1px solid var(--light-border-color);
}

.sc-question .se-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  cursor: pointer;
}

.sc-question .se-heading .se-label {
  flex: 1;
}

.sc-question .se-heading .se-arrow {
  display: none;
  position: relative;
  width: var(--double-spacing);
  height: var(--double-spacing);
  margin-left: var(--default-spacing);
}

.sc-question .se-heading .se-arrow span {
  position: absolute;
  top: 12px;
  width: 14px;
  height: 2px;
  background-color: var(--light-border-color);
}

.sc-question .se-heading .se-arrow span:first-of-type {
  left: 0;
  transform: rotate(45deg);
}

.sc-question .se-heading .se-arrow span:last-of-type {
  right: 0;
  transform: rotate(-45deg);
}

.sc-question .se-heading .se-arrow.sm-opened span:first-of-type {
  transform: rotate(-45deg);
}

.sc-question .se-heading .se-arrow.sm-opened span:last-of-type {
  transform: rotate(45deg);
}

.sc-question .se-answer {
  margin-top: var(--default-spacing);
}

.sc-question .se-answer > * {
  margin: 0 auto var(--default-spacing);
}