.sc-collaborator {
  display: flex;
  align-items: center;
  @include ts-interface;
}

.sc-collaborator > .se-name,
.sc-collaborator > .se-status {
  flex: 1;
}

.sc-collaborator > .se-status {
  @include ts-quiet;
}

.sc-collaborator > .se-status.sm-invited {
  color: #d6a975;
}

.sc-collaborator > .se-user-icon {
  margin-right: var(--default-spacing);
}

.sc-collaborator > .se-user-icon > i.fa {
  border-radius: var(--quad-spacing);
  border: 1px solid var(--light-border-color);
  padding: var(--default-spacing);
}

.sc-collaborator > .se-action > .sc-button {
  color: rgb(var(--primary-color));
}