.sc-comment {
  display: flex;
  padding: var(--double-spacing) 0;
}

.sc-comment > .se-user-icon {
  margin-right: var(--default-spacing);
}

.sc-comment > .se-user-icon > i.fa {
  border-radius: var(--quad-spacing);
  border: 1px solid var(--light-border-color);
  padding: var(--default-spacing);
}

.sc-comment > .se-comment-wrapper {
  display: flex;
  flex-direction: column;
}

.sc-comment > .se-comment-wrapper > .se-comment-header {
  display: flex;
  @include ts-interface;
}

.sc-comment > .se-comment-wrapper > .se-comment-header > .se-divider {
  margin: 0 var(--half-spacing);
}

.sc-comment > .se-comment-wrapper > .se-comment-header > .se-datetime {
  @include ts-quiet;
}

.sc-comment > .se-comment-wrapper > .se-comment {
  @include ts-small-content;
}