.sc-content-body {
  padding: var(--quad-spacing);
}

.sc-content-body > .sc-limiter > * {
  margin: var(--default-spacing) 0;
}

.sc-content-body .sc-paragraph {
  @include ts-content;
}

.sc-content-body a {
  color: var(--link-color);
  text-decoration: none;
}

.sc-content-body.sm-centered {
  text-align: center;
}