.sc-essay {
  @include ts-content;
}

.sc-essay .se-essay-content {
  max-width: 700px;
  margin: 0 auto;
  background: #fff;
  padding-top: var(--double-spacing);
}

.sc-essay .se-essay-content > * {
  margin: var(--double-spacing) 0;
}

.sc-essay .se-essay-content > .sc-collection > * {
  margin-bottom: var(--double-spacing);
}

.sc-essay .se-essay-content > .sc-collection > .sc-heading {
  padding-bottom: var(--default-spacing);
  margin-bottom: 0px;
}

/* Extra space for top level headings (level-2) */
.sc-essay .se-essay-content > .sc-collection > .sc-heading.sm-level-2 {
  padding-top: var(--double-spacing);
}

/* */
.sc-essay .se-essay-content ::selection {
  background-color: rgba(var(--primary-color), 0.7);
  color: #fff;
}