/*
  Header
  ---------------------------------
*/

.sc-header {
  height: var(--header-height);
  border-bottom: 1px solid var(--light-border-color);
}

.sc-header.sm-inverted {
  border-bottom: none;
}

.sc-header > .sc-limiter {
  height: 100%;
}

.sc-header.sm-website-header {
  padding-top: 50px;
}