.sc-switch {
  @include ts-interface;
  display: flex;
  align-items: center;
  cursor: pointer;
  /* Attention: user-select=none is problematic for us, because then we do not receive mousedown events anymore */
  user-select: none;
}

.sc-switch > input {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

.sc-switch > span.se-switch {
  display: inline-block;
  position: relative;
  background: #CFD1CB;
  width: 36px;
  height: 14px;
  border-radius: 36px;
}

.sc-switch > input:checked + span.se-switch {
  background: rgba(var(--primary-color),0.3);
}

.sc-switch > span.se-switch:after {
  content: '';
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background: #fff;
  align-self: center;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  border-radius: 100%;
  box-shadow: 0 2px 4px rgba(72, 72, 72, 0.5);
}

.sc-switch > input:checked + span.se-switch:after {
  left: calc(100% - 20px);
  background-color: rgb(var(--primary-color));
  box-shadow: 0 2px 4px rgba(145, 145, 145, 0.5);
}

.sc-switch > .se-label {
  margin-left: var(--default-spacing);
}