.sc-user-search-item {
  display: flex;
  align-items: center;
}

.sc-user-search-item > .se-user-info {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.sc-user-search-item > .se-user-info > .se-username {
  @include ts-small-interface;
  @include ts-quiet;
}

.sc-user-search-item.sm-selected > .se-user-info > .se-username,
.sc-user-search-item:hover > .se-user-info > .se-username {
  color: #fff;
}

.sc-user-search-item > .se-user-icon {
  margin-right: var(--default-spacing);
}

.sc-user-search-item > .se-user-icon > i.fa {
  border-radius: var(--quad-spacing);
  border: 1px solid var(--light-border-color);
  padding: var(--default-spacing);
}

.sc-user-search-item.sm-selected > .se-user-icon > i.fa,
.sc-user-search-item:hover > .se-user-icon > i.fa {
  border: 1px solid #fff;
}