.sc-editor-toolbar {
  box-shadow: 0 0 0 0.75pt #d1d1d1, 0 0 3pt 0.75pt #ccc;
  background: #fff;
  z-index: 1;
  height: 40px;
  flex-shrink: 0;
  flex-grow: 0;
}

.sc-editor-toolbar .sc-horizontal-stack > * {
  margin: 0 1px;
}

.sc-editor-toolbar > * {
  height: 100%;
}

.sc-editor-toolbar .sc-button {
  min-width: var(--small-button-height);
}