.sc-teaser .se-page-heading {
  max-width: 960px;
  margin: 0px auto;
  padding-top: 100px;
  padding-bottom: 200px;
  @include ts-heading-1;
  @include ts-inverted;
  text-align: center;
  line-height: 61px; // override h1 line-height for teaser
}

.sc-teaser.sm-size-small .se-page-heading {
  padding-top: 50px;
  padding-bottom: 100px;
}

.sc-teaser.sm-size-large .se-page-heading {
  padding-top: 150px;
  padding-bottom: 250px;
}

.sc-teaser .se-page-heading .se-highlight {
  background: #fff;
  color: var(--heading-color);
  padding: 0 5px;
}

// Mobile overrides
.sc-teaser.sm-mobile .se-page-heading {
  padding-top: 40px;
  padding-bottom: 120px;
}

.sc-teaser.sm-mobile.sm-size-small .se-page-heading {
  padding-top: 20px;
  padding-bottom: 80px;
}

.sc-teaser.sm-mobile.sm-size-large .se-page-heading {
  padding-top: 20x;
  padding-bottom: 80px;
}