/*
  Footer
  ---------------------------------
*/

.sc-footer {
  border-top: 1px solid var(--light-border-color);
}

.sc-footer .se-footer-container {
  padding: var(--double-spacing) 0;
  @include ts-small-interface;
}

.sc-footer .se-footer-menu {
  padding-bottom: var(--double-spacing);
  margin-bottom: var(--double-spacing);
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.sc-footer a {
  color: inherit;
  text-decoration: none;
}

.sc-footer .se-footer-menu .se-nav-header {
  text-transform: uppercase;
}

.sc-footer .se-footer-menu .se-nav-item {
  margin-top: var(--half-spacing);
  @include ts-interface;
  @include ts-quiet;
}

.sc-footer .se-footer-second-menu {
  @include ts-interface;
  @include ts-quiet;
}

.sc-footer .se-footer-second-menu .se-item {
  margin-left: var(--default-spacing);
}

.sc-footer.sm-mobile .se-footer-menu {
  flex-wrap: wrap;
}

.sc-footer.sm-mobile .se-footer-menu .se-menu {
  width: 50%;
  margin-bottom: var(--double-spacing);
}