/*
  Sign In
  ---------------------------------
*/

.sc-sign-in {
  min-height: var(--content-min-height);
  padding: var(--double-spacing) 0;
  background-color: var(--substance-background);
}

.sc-sign-in > * {
  margin-bottom: 40px
}

.sc-sign-in > .sc-primary-button,
.sc-sign-in > .se-description {
  margin-top: 20px;
}

.sc-sign-in > .se-description {
}

.sc-input .se-input {
  width: 100%;
  max-width: var(--input-max-width);
  height: var(--input-height);
  box-sizing: border-box;
}
