.sc-badge > * {
  background-color: #fff;
}

.sc-badge .se-badge-image img {
  display: block;
  max-width: 100%;
}

.sc-badge > .sc-card {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  padding: var(--half-spacing) var(--default-spacing);
}

.sc-badge > .sc-card > * {
  padding: var(--half-spacing) 0;
}